.ham_burger_icon{
    padding-top: 10px;
    padding-bottom: 10px;
    display: none;
}
.hub{
    color: #008DFF;
}
.right_nav{
    margin-left: 10px;
    
}
.quickpay{
    background-color: #2E86C1 !important;
    margin-left: 10px !important;
    width: fit-content;
}
.nav_option{
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: 2px solid #ffffff;
    color: #000 !important;
}
.isActive_inNav{
    padding-top: 15px;
    padding-bottom: 15px;
    color:#2E86C1 !important;
    border-bottom: 2px solid #2E86C1;
}
.navbarTop{
    background-color: #fff;
}


@media only screen and (max-width: 991px) {
    .ham_burger_icon{
        display: block;
    }
    .right_nav{
        margin-left: 0px;
        width: 100%;
    }
    .quickpay{
        margin: 10px 10px 10px 10px !important;
        width: 100% !important;
    }
    
    .nav_option{
        padding-top: 15px;
        padding-bottom: 15px;
        padding-left: 17px !important;
    }
    .isActive_inNav{
        padding-top: 15px;
        padding-bottom: 15px;
        padding-left: 15px !important;
        color:#2E86C1 !important;
        border-bottom: 2px solid #ffffff;
        border-left: 2px solid #2E86C1;
    }
}