*{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.cursor-pointer{
  cursor: pointer;
}
.boxShadow{
  box-shadow: 0 8px 16px 0 rgb(255, 255, 255), 0 12px 100px 0 rgba(255, 255, 255, 0.911) !important;
}
.main_container{
  height: 100vh;
}
.brand-name{
  color:#2E86C1;
}
a{
  text-decoration: none !important;
}
.table_plan{
  width: 100% !important;
  overflow-x: auto;
}
.btn_grp{
  max-width:450px;
  margin-top:4px;
}

@media only screen and (min-width: 991px) {
  ::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background-color: #2e86c109;
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #929292af; 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #b5b5b5b2; 
  }
}